	module.exports = {"data":{"site":{"siteMetadata":{"title":":-)","author":"github.com/9renpoto"}},"markdownRemark":{"id":"/home/travis/build/9renpoto/entry/src/pages/2016-09-05-study-motivation/index.md absPath of file >>> MarkdownRemark","html":"<h1>勉強する</h1>\n<p>勉強について考える。</p>\n<h2>TL;DR</h2>\n<ul>\n<li>勉強している人は本当に偉い</li>\n<li>何事も尊敬できることは才能より努力</li>\n</ul>\n<h2>勉強から得られること</h2>\n<ul>\n<li>出来ないことができるようになる</li>\n<li>出来る人のことを尊敬できるようになる</li>\n<li>\n<p>人間の時間は有限</p>\n<ul>\n<li>天才はいるが天才にはなれない</li>\n<li>効率化は二の次</li>\n</ul>\n</li>\n<li>\n<p>遊ぶことも勉強</p>\n<ul>\n<li>新しい知識を得ることは楽しい</li>\n<li>出来ないことができるようになるため努力が必要</li>\n<li>自分と相性の良い天性のことなんて存在した試しがない</li>\n</ul>\n</li>\n</ul>\n<h2>まとめ</h2>\n<ul>\n<li>勉強は楽しい</li>\n<li>時間は有限</li>\n<li>求められたら勉強する</li>\n</ul>\n<h2>余談</h2>\n<p>Overwatchで勝てないのが厳しい。</p>","frontmatter":{"title":"勉強を続けること","date":"September 04, 2016"}}},"pathContext":{"path":"/entry/2016/09/05/study-motivation/","_PARENT":"SOURCE"}}

